import Vue from 'vue'
import Router from 'vue-router'
import index from '@/views/index'
import uc_index from '@/views/uc_index'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'index',
      component: index
    },
    {
      path: '/index',
      name: 'index',
      component: index
    },
    {
      path: '/uc_index',
      name: 'uc_index',
      component: uc_index
    }
  ]
})



